/* src/main/frontend/src/pages/SubjectInfoPage.css */

.subjectInfo-page {
    width: 90%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

.subjects-table {
    width: 100%;
    border-collapse: collapse;
}

.subjects-table th,
.subjects-table td {
    border: 1px solid #ccc;
    padding: 8px;
    text-align: center;
}

.subjects-table th {
    background-color: #f4f4f4;
}
