/* src/main/frontend/src/components/NavBar.css */

.navbar {
    width: 300px;
    background-color: #282c34;
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    position: fixed;
    top: 0;
    left: 0;
}

.navbar-list {
    list-style-type: none;
    padding: 0;
    width: 100%;
    margin: 0;
}

.navbar-item {
    width: 100%;
    margin-bottom: 15px; /* 링크 간의 간격 설정 */
}

.navbar-link {
    display: flex;
    align-items: center;
    padding: 10px 20px;
    color: white;
    text-decoration: none;
    transition: background-color 0.2s;
    font-size: 24px; /* 폰트 크기 설정 */
}

.navbar-link:hover {
    background-color: #9370db;
    box-shadow: 0 4px 8px rgba(106, 77, 171, 0.8); /* 더 진한 그림자 색상 추가 */
}

.navbar-icon {
    margin-right: 10px;
    font-size: 30px; /* 아이콘 크기 설정 */
}
